.main-block {
  position: relative;
  padding-top: 153px;
  background: url("../img/main-desk.jpg") no-repeat;
  background-position: right -42px top -100px;
  background-color: $dark-blue;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 448px;
    background: linear-gradient(to top, #141414, transparent);
    opacity: 0.6;
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/main-desk@2x.jpg");
  }
}

.main-block__index {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.main-block__title {
  margin: 0 0 25px;
  max-width: 100%;
  font-size: 72px;
  line-height: 86px;
  font-weight: 900;
  font-family: $raleway;
  letter-spacing: 3.6px;
  text-transform: uppercase;
  color: white;
}

.main-block__desc {
  display: none;
}

.main-block__description {
  margin: 0 0 54px;
  padding-bottom: 0;
  max-width: 1050px;
  font-size: 24px;
  line-height: 38px;
  font-weight: 500;
  color: white;
}

.main-block__green {
  color: $light-green;
}


.main-block__btn {
  margin-bottom: 110px;
  display: flex;
  align-items: baseline;
}

.main-block__services-btn,
.main-block__contact-btn {
  z-index: 1;
  padding: 14px 0;
  flex: 0 0 300px;
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;

  &:hover,
  &:focus {
    box-shadow: 0 2px 6px 1px rgba(0, 255, 216, 0.65);
  }
}

.main-block__contact-btn {
  background-color: transparent;
  border: 1px solid $light-green;
  color: white;
}

.main-block__services-btn {
  margin-right: 22px;
}

.main-block__features {
  order: 5;
  padding-bottom: 78px;
}

@media (max-width: $desktop-width) {
  .main-block {
    padding-top: 0;
    background: none;

    &::before {
      display: none;
    }
  }

  .main-block__wrapper {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 448px;
      background: linear-gradient(to top, #141414, transparent);
      opacity: 0.6;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: url("../img/main-tabl.jpg") no-repeat;
      background-size: cover;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/main-tabl@2x.jpg");
      }
    }
  }

  .main-block__index {
    position: relative;
    padding: 124px 66px 0 80px;
    min-height: 600px;
  }

  .main-block__title {
    margin: 0 0 20px;
    max-width: 620px;
    font-size: 38px;
    line-height: 44px;
    letter-spacing: 2px;
    text-align: left;
  }

  .main-block__desc {
    display: none;
  }

  .main-block__description {
    margin: 0 0 82px;
    padding: 0;
    max-width: 600px;
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
  }

  .main-block__btn {
    order: 4;
    margin-bottom: 0;
  }

  .main-block__services-btn,
  .main-block__contact-btn {
    flex: 0 0 290px;
    font-size: 16px;
  }

  .main-block__services-btn {
    margin-bottom: 0;
  }

  .main-block__features {
    background-color: white;
    padding-bottom: 0;
  }
}

@media (max-width: $tablet-width) {
  .main-block__wrapper::before {
    top: -23px;
    height: 80%;
    z-index: -1;
    background: url("../img/main-mob.jpg") no-repeat;
    background-color: black;
    background-size: cover;
    background-position: right;
  }

  .main-block__wrapper::after {
    display: none;
  }

  .main-block__index {
    padding: 131px 20px 0;
    min-height: auto;

    &::before {
      background: none;
    }
  }

  .main-block__title {
    margin: 0 auto 19px;
    order: 1;
    max-width: 280px;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.8px;
    text-align: center;
  }

  .main-block__desc {
    display: block;
    margin: 0 auto 59px;
    order: 2;
    max-width: 250px;
    font-size: 16px;
    font-weight: 400;
    font-family: $roboto;
    text-align: center;
    color: white;

    &--hidden span {
      color: $light-green;
    }
  }

  .main-block__btn {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 50px;
    order: 3;
  }

  .main-block__description {
    margin: 0 -20px;
    padding: 36px 20px 45px;
    order: 4;
    font-size: 12px;
    line-height: 18px;
    max-width: inherit;
    color: black;
    background-color: white;
  }

  .main-block__green {
    color: black;
  }

  .main-block__services-btn,
  .main-block__contact-btn {
    padding: 17px 0;
    width: 100%;
  }

  .main-block__services-btn {
    margin-bottom: 10px;
  }

  .main-block__features {
    padding-bottom: 0;
    order: 6;
  }
}
